import {Container, filters, Graphics, Sprite, WRAP_MODES} from "pixi.js";
import {CreateGradient} from './helper/gradient';
import {AdvancedBloomFilter} from 'pixi-filters'
import {Ellipse} from './helper/point';

export class Sun {
  /**
   *
   * @param {Game} game
   * @param {number} radius
   * @param {PIXI.Container} container
   */
  constructor(game, container, radius) {
    this._radius = radius;
    this.sprite = new Container();
    this.sunGraphic = new Graphics();
    this.sprite.position.set(this._radius * 2 + game.padding, this._radius * 2 + game.padding);
    this.angle = Math.PI * 2;
    this.origin = {x: game.width / 2, y: game.height - game.groundHeight + 200};
    this.ellipseX = game.width / 3;
    this.ellipseY = game.height * 1.5;
    this.updatePosition();
    this.updateGraphic([{color: '#e5eaff', offset: 0}, {color: '#fde4da', offset: 1}], radius);
    const displacementSprite = Sprite.from('data:image/png;base64,');
    displacementSprite.texture.baseTexture.wrapMode = WRAP_MODES.REPEAT;
    this.displacementFilter = new filters.DisplacementFilter(displacementSprite);
    this.bloomFilter = new AdvancedBloomFilter();
    this.bloomFilter.padding = 200;
    this.updateBloom({
      threshold: 0.15,
      bloomScale: 2.5,
      brightness: 0.8,
      blur: 40,
      quality: 10
    });
    this.sunGraphic.filters = [this.displacementFilter, this.bloomFilter];
    this.updateFilterDisplacement(0);
    this.sprite.addChild(this.sunGraphic);
    this.sprite.addChild(displacementSprite);
    container.addChild(this.sprite);
    game.app.ticker.add(() => {
      if (this.angle < 0) {
        this.angle += Math.PI * 2;
      }
      this.angle -= 0.0001;
      this.updatePosition();
      displacementSprite.x += 0.1;
      displacementSprite.y += 0.1;
      if (displacementSprite.x > displacementSprite.width) displacementSprite.x = 0;
      if (displacementSprite.y > displacementSprite.height) displacementSprite.y = 0;
    });
  }
  updatePosition(){
    const position = Ellipse(this.ellipseX, this.ellipseY, this.origin, this.angle);
    this.sprite.position.set(position.x, position.y);
  }

  /**
   *
   * @param {Object} options
   * @param {number} [options.threshold]
   * @param {number} [options.bloomScale]
   * @param {number} [options.brightness]
   * @param {number} [options.blur]
   * @param {number} [options.quality]
   */
  updateBloom(options) {
    if (!options) {
      return
    }
    if (typeof options.threshold !== 'undefined') this.bloomFilter.threshold = options.threshold;
    if (typeof options.bloomScale !== 'undefined') this.bloomFilter.bloomScale = options.bloomScale;
    if (typeof options.brightness !== 'undefined') this.bloomFilter.brightness = options.brightness;
    if (typeof options.blur !== 'undefined') this.bloomFilter.blur = options.blur;
    if (typeof options.quality !== 'undefined') this.bloomFilter.quality = options.quality;
  }

  updateFilterDisplacement(power) {
    if (typeof power !== 'undefined') {
      this.power = power;
    }
    if (typeof this.power === 'undefined') {
      return;
    }
    this.displacementFilter.scale.x = this.power;
    this.displacementFilter.scale.y = this.power;
  }

  /**
   * @param {Object[]} colors
   * @param {string} colors.color
   * @param {number} colors.offset
   * @param radius
   */
  updateGraphic(colors, radius) {
    if (colors) {
      this._texture = CreateGradient(this._radius * 2, colors);
    }
    if (radius) {
      this._radius = radius;
    }
    this.sunGraphic.clear();
    this.sunGraphic.beginTextureFill({texture: this._texture});
    this.sunGraphic.drawCircle(0, 0, this._radius);
    this.sunGraphic.endFill();
    this.updateFilterDisplacement();
  }
}
